import { Component, Input, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { CardData } from '../card-data';
import { TreatmentCardFemalePregnantComponent } from '../treatment-card-female-pregnant/treatment-card-female-pregnant.component';
import { TreatmentCardMedicationComponent } from '../treatment-card-medication/treatment-card-medication.component';
import { TreatmentCardComponent } from '../treatment-card/treatment-card.component';
import { MatExpansionModule } from '@angular/material/expansion';
import { CircleProgressComponent, CircleProgressOptions } from 'ng-circle-progress';

@Component({
  selector: 'app-medical-search-results-sidenav',
  templateUrl: './medical-search-results-sidenav.component.html',
  styleUrls: ['./medical-search-results-sidenav.component.css']
})
export class MedicalSearchResultsSidenavComponent implements OnInit {

  @Input() cardData!: CardData;
  @Input() percentage!: number;
  @Input() diseaseShow!: boolean;
  @Input() diseaseShow2!: boolean;
  @Input() titlesShow!: boolean;
  treatmentConfig = new MatDialogConfig();
  panelOpenState = false;
  badgeColors=['primary', 'accent', 'warn'];
  constructor(private dialog: MatDialog) { }
  currentTab: number = 0;
  ngOnInit(): void {
  }

  openTreatmentCard(cardId: string){
    this.treatmentConfig.disableClose = false;
    this.treatmentConfig.autoFocus = true;
    this.treatmentConfig.width = "60%";
    this.treatmentConfig.height = "80%";
    switch(cardId){
      case "pregnantfemale":
        this.dialog.open(TreatmentCardFemalePregnantComponent,this.treatmentConfig);
        break;
      case "nonmedication":
        this.dialog.open(TreatmentCardComponent,this.treatmentConfig);
        break;
      case "medication":
        this.dialog.open(TreatmentCardMedicationComponent,this.treatmentConfig);
        break;  
    }
  }

  openGroup(tab: number){
    this.currentTab=tab;
  }
}
